/* @jsx jsx */
import {jsx} from "@emotion/react"

import * as styles from "./ProjectRow.styles";

const NameOrLink = (props: { name: string, url?: string }): JSX.Element => {
    const {name, url} = props;
    if (name && url) {
        return <a href={url}>{name}</a>
    }
    return <div>{name}</div>
}

export interface ProjectRowProps {
    projectName: string;
    projectUrl?: string;
    institutionName?: string;
    institutionUrl?: string;
    tags?: string[];
}

const ProjectRow = (prop: ProjectRowProps) => {
    const {projectName, projectUrl, institutionName, institutionUrl, tags} = prop;

    let tagsComponent = null;
    if (tags) {
        tagsComponent = <div>
            {tags.map(tag => {
                return <div css={styles.tag}>{tag}</div>
            })}
        </div>
    }

    return (<div css={styles.container}>
        <div css={styles.bulletCell}>-</div>
        <div>
            <div>
                <NameOrLink name={projectName} url={projectUrl}/>
            </div>
            <div>
                <i>
                    <NameOrLink name={institutionName} url={institutionUrl}/>
                </i>
            </div>
            <div>
                {tagsComponent && tagsComponent}
            </div>
        </div>
    </div>)
}

export default ProjectRow;
